interface LazyLoadOptions {
	root?: Element | null;
	rootMargin?: string;
	threshold?: number | number[];
	onEnter?: (entry: IntersectionObserverEntry) => void;
	onExit?: (entry: IntersectionObserverEntry) => void;
	once?: boolean;
}

class IntersectionManager {
	private observers: Map<string, IntersectionObserver> = new Map();
	private elements: Map<Element, string> = new Map();

	createObserver(options: LazyLoadOptions): string {
		const observerId = `observer-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;

		const observer = new IntersectionObserver(
			(entries) => {
				entries.forEach((entry) => {
					if (entry.isIntersecting) {
						options.onEnter?.(entry);

						if (options.once) {
							observer.unobserve(entry.target);
							this.elements.delete(entry.target);
						}
					} else {
						options.onExit?.(entry);
					}
				});
			},
			{
				root: options.root,
				rootMargin: options.rootMargin || '50px',
				threshold: options.threshold || 0.1
			}
		);

		this.observers.set(observerId, observer);
		return observerId;
	}

	observe(observerId: string, element: Element) {
		const observer = this.observers.get(observerId);
		if (observer) {
			observer.observe(element);
			this.elements.set(element, observerId);
		}
	}

	unobserve(element: Element) {
		const observerId = this.elements.get(element);
		if (observerId) {
			const observer = this.observers.get(observerId);
			observer?.unobserve(element);
			this.elements.delete(element);
		}
	}

	disconnect(observerId: string) {
		const observer = this.observers.get(observerId);
		if (observer) {
			observer.disconnect();
			this.observers.delete(observerId);
		}
	}

	disconnectAll() {
		this.observers.forEach((observer) => observer.disconnect());
		this.observers.clear();
		this.elements.clear();
	}
}

// Singleton instance
export const intersectionManager = new IntersectionManager();

// Convenience function for lazy loading images
export function setupImageLazyLoading(
	imageElement: HTMLImageElement,
	src: string,
	placeholder?: string
) {
	// Set up placeholder
	if (placeholder) {
		imageElement.src = placeholder;
	}

	const observerId = intersectionManager.createObserver({
		rootMargin: '100px',
		threshold: 0.1,
		once: true,
		onEnter: () => {
			// Create a new image to preload
			const tempImage = new Image();
			tempImage.onload = () => {
				imageElement.src = src;
				imageElement.classList.add('loaded');
			};
			tempImage.onerror = () => {
				imageElement.classList.add('error');
			};
			tempImage.src = src;
		}
	});

	intersectionManager.observe(observerId, imageElement);

	// Return cleanup function
	return () => {
		intersectionManager.unobserve(imageElement);
		intersectionManager.disconnect(observerId);
	};
}

// Svelte action for lazy loading
export function lazyLoad(node: HTMLElement, options: LazyLoadOptions & { src?: string }) {
	let cleanup: (() => void) | null = null;

	if (node instanceof HTMLImageElement && options.src) {
		cleanup = setupImageLazyLoading(node, options.src);
	} else {
		const observerId = intersectionManager.createObserver(options);
		intersectionManager.observe(observerId, node);
		cleanup = () => {
			intersectionManager.unobserve(node);
			intersectionManager.disconnect(observerId);
		};
	}

	return {
		destroy() {
			cleanup?.();
		}
	};
}
