/**
 * Svelte action for TV navigation
 * Makes any element focusable and navigable with D-pad
 */

import { getTVNavigation } from '$lib/utils/tv-navigation';
import { getIsAndroidTV } from '$lib/utils/tv-detection';

interface TVNavigationOptions {
	row: number;
	column: number;
	id?: string;
	disabled?: boolean;
	onSelect?: () => void;
	focusOnMount?: boolean;
}

/**
 * Svelte action to make an element TV navigable
 * 
 * Usage:
 * <button use:tvNavigable={{ row: 0, column: 0, onSelect: handleClick }}>
 *   Click me
 * </button>
 */
export function tvNavigable(element: HTMLElement, options: TVNavigationOptions) {
	// Only enable on Android TV
	if (!getIsAndroidTV()) {
		return {
			destroy() {}
		};
	}

	const navigation = getTVNavigation();
	
	// Register the element
	navigation.registerElement(element, options.row, options.column, {
		id: options.id,
		onSelect: options.onSelect,
		disabled: options.disabled
	});

	// Focus on mount if requested
	if (options.focusOnMount) {
		setTimeout(() => {
			element.focus();
		}, 100);
	}

	return {
		update(newOptions: TVNavigationOptions) {
			// Unregister and re-register with new options
			navigation.unregisterElement(element);
			navigation.registerElement(element, newOptions.row, newOptions.column, {
				id: newOptions.id,
				onSelect: newOptions.onSelect,
				disabled: newOptions.disabled
			});
		},
		
		destroy() {
			navigation.unregisterElement(element);
		}
	};
}

/**
 * Svelte action to create a TV navigation grid
 * Automatically assigns row/column positions to child elements
 * 
 * Usage:
 * <div use:tvGrid={{ columns: 4, startRow: 0 }}>
 *   <button>Item 1</button>
 *   <button>Item 2</button>
 *   <button>Item 3</button>
 * </div>
 */
export function tvGrid(element: HTMLElement, options: { columns: number; startRow?: number; selector?: string }) {
	if (!getIsAndroidTV()) {
		return { destroy() {} };
	}

	const navigation = getTVNavigation();
	const { columns, startRow = 0, selector = 'button, [data-tv-navigable]' } = options;
	
	function setupGrid() {
		const children = element.querySelectorAll(selector) as NodeListOf<HTMLElement>;
		
		children.forEach((child, index) => {
			const row = startRow + Math.floor(index / columns);
			const column = index % columns;
			
			navigation.registerElement(child, row, column, {
				id: `grid-${startRow}-${index}`
			});
		});
	}

	// Initial setup
	setupGrid();

	// Observer for dynamic content
	const observer = new MutationObserver(() => {
		// Clear existing registrations for this grid
		const children = element.querySelectorAll(selector) as NodeListOf<HTMLElement>;
		children.forEach(child => navigation.unregisterElement(child));
		
		// Re-setup grid
		setupGrid();
	});

	observer.observe(element, { childList: true, subtree: true });

	return {
		update(newOptions: { columns: number; startRow?: number; selector?: string }) {
			// Clear existing registrations
			const children = element.querySelectorAll(selector) as NodeListOf<HTMLElement>;
			children.forEach(child => navigation.unregisterElement(child));
			
			// Setup with new options
			Object.assign(options, newOptions);
			setupGrid();
		},
		
		destroy() {
			observer.disconnect();
			const children = element.querySelectorAll(selector) as NodeListOf<HTMLElement>;
			children.forEach(child => navigation.unregisterElement(child));
		}
	};
}

/**
 * Svelte action for TV focus management
 * Handles focus within a container
 */
export function tvFocusContainer(element: HTMLElement, options: { trapFocus?: boolean; initialFocus?: string } = {}) {
	if (!getIsAndroidTV()) {
		return { destroy() {} };
	}

	const navigation = getTVNavigation();

	// Set initial focus if specified
	if (options.initialFocus) {
		setTimeout(() => {
			navigation.focusById(options.initialFocus!);
		}, 100);
	}

	// Trap focus within container if enabled
	if (options.trapFocus) {
		const handleKeyDown = (event: KeyboardEvent) => {
			if (event.key === 'Tab') {
				event.preventDefault();
				// Handle tab navigation within container
			}
		};

		element.addEventListener('keydown', handleKeyDown);

		return {
			destroy() {
				element.removeEventListener('keydown', handleKeyDown);
			}
		};
	}

	return { destroy() {} };
}