interface PerformanceMetric {
	name: string;
	value: number;
	timestamp: number;
}

interface PerformanceThresholds {
	fcp: number; // First Contentful Paint
	lcp: number; // Largest Contentful Paint
	fid: number; // First Input Delay
	cls: number; // Cumulative Layout Shift
}

class PerformanceMonitor {
	private metrics: PerformanceMetric[] = [];
	private thresholds: PerformanceThresholds = {
		fcp: 1500, // 1.5 seconds
		lcp: 2500, // 2.5 seconds
		fid: 100, // 100ms
		cls: 0.1 // 0.1 CLS score
	};

	constructor() {
		if (typeof window !== 'undefined') {
			this.initializeObservers();
		}
	}

	private initializeObservers() {
		// Observe Core Web Vitals
		if ('PerformanceObserver' in window) {
			// First Contentful Paint
			this.observeMetric('first-contentful-paint', 'paint');

			// Largest Contentful Paint
			this.observeMetric('largest-contentful-paint', 'largest-contentful-paint');

			// First Input Delay
			this.observeMetric('first-input-delay', 'first-input');

			// Cumulative Layout Shift
			this.observeMetric('cumulative-layout-shift', 'layout-shift');
		}
	}

	private observeMetric(metricName: string, entryType: string) {
		try {
			const observer = new PerformanceObserver((list) => {
				for (const entry of list.getEntries()) {
					let value = 0;

					switch (entryType) {
						case 'paint':
							value = entry.startTime;
							break;
						case 'largest-contentful-paint':
							value = entry.startTime;
							break;
						case 'first-input':
							value = (entry as any).processingStart - entry.startTime;
							break;
						case 'layout-shift':
							value = (entry as any).value;
							break;
					}

					this.recordMetric(metricName, value);
				}
			});

			observer.observe({ entryTypes: [entryType] });
		} catch (error) {
			console.warn(`Failed to observe ${metricName}:`, error);
		}
	}

	recordMetric(name: string, value: number) {
		const metric: PerformanceMetric = {
			name,
			value,
			timestamp: Date.now()
		};

		this.metrics.push(metric);

		// Check thresholds
		this.checkThreshold(name, value);

		// Limit stored metrics to prevent memory issues
		if (this.metrics.length > 100) {
			this.metrics = this.metrics.slice(-50);
		}
	}

	private checkThreshold(name: string, value: number) {
		let threshold = 0;
		let unit = 'ms';

		switch (name) {
			case 'first-contentful-paint':
				threshold = this.thresholds.fcp;
				break;
			case 'largest-contentful-paint':
				threshold = this.thresholds.lcp;
				break;
			case 'first-input-delay':
				threshold = this.thresholds.fid;
				break;
			case 'cumulative-layout-shift':
				threshold = this.thresholds.cls;
				unit = 'score';
				break;
		}

		if (threshold > 0 && value > threshold) {
			console.warn(
				`Performance threshold exceeded for ${name}: ${value}${unit} > ${threshold}${unit}`
			);
		}
	}

	// Mark custom performance points
	mark(name: string) {
		if ('performance' in window && 'mark' in performance) {
			performance.mark(name);
		}
	}

	// Measure time between marks
	measure(name: string, startMark: string, endMark: string) {
		if ('performance' in window && 'measure' in performance) {
			try {
				performance.measure(name, startMark, endMark);
				const measure = performance.getEntriesByName(name, 'measure')[0];
				this.recordMetric(name, measure.duration);
			} catch (error) {
				console.warn(`Failed to measure ${name}:`, error);
			}
		}
	}

	getMetrics(): PerformanceMetric[] {
		return [...this.metrics];
	}

	getMetricsByName(name: string): PerformanceMetric[] {
		return this.metrics.filter((metric) => metric.name === name);
	}

	// Get bundle size information
	getBundleInfo() {
		if ('performance' in window) {
			const navigationEntries = performance.getEntriesByType('navigation');
			const resourceEntries = performance.getEntriesByType('resource');

			return {
				navigation: navigationEntries,
				resources: resourceEntries.map((entry) => ({
					name: entry.name,
					size: (entry as any).transferSize || 0,
					duration: entry.duration
				}))
			};
		}
		return null;
	}
}

// Singleton instance
export const performanceMonitor = new PerformanceMonitor();

// Utility functions for component performance tracking
export function trackComponentMount(componentName: string) {
	performanceMonitor.mark(`${componentName}-mount-start`);
}

export function trackComponentMountComplete(componentName: string) {
	performanceMonitor.mark(`${componentName}-mount-end`);
	performanceMonitor.measure(
		`${componentName}-mount-duration`,
		`${componentName}-mount-start`,
		`${componentName}-mount-end`
	);
}

// Track image loading performance
export function trackImageLoad(imageSrc: string, startTime: number) {
	const duration = performance.now() - startTime;
	performanceMonitor.recordMetric('image-load-time', duration);
}
