<script lang="ts">
	import { onMount } from 'svelte';
	import { lazyLoad } from '$lib/utils/intersection';
	import { trackImageLoad } from '$lib/performance/monitor';

	export let src: string;
	export let alt: string = '';
	export let placeholder: string = '';
	export let webpSrc: string = '';
	export let width: number | string = 'auto';
	export let height: number | string = 'auto';
	export let loading: 'lazy' | 'eager' = 'lazy';
	export let sizes: string = '';
	export let srcset: string = '';
	export let className: string = '';

	let imageElement: HTMLImageElement;
	let isLoaded = false;
	let hasError = false;
	let isIntersecting = false;
	let loadStartTime = 0;

	// Generate responsive srcset if not provided
	$: responsiveSrcset = srcset || generateResponsiveSrcset(src);

	// Generate WebP srcset
	$: webpSrcset = webpSrc ? generateResponsiveSrcset(webpSrc) : '';

	onMount(() => {
		if (loading === 'eager') {
			loadImage();
		}
	});

	function generateResponsiveSrcset(baseSrc: string): string {
		if (!baseSrc) return '';

		const widths = [320, 640, 960, 1280, 1920];
		return widths.map((w) => `${baseSrc}?w=${w} ${w}w`).join(', ');
	}

	function loadImage() {
		if (isLoaded || hasError) return;

		loadStartTime = performance.now();
		isIntersecting = true;
	}

	function handleLoad() {
		isLoaded = true;
		if (loadStartTime > 0) {
			trackImageLoad(src, loadStartTime);
		}
	}

	function handleError() {
		hasError = true;
		console.warn(`Failed to load image: ${src}`);
	}

	function handleIntersection() {
		if (!isIntersecting && loading === 'lazy') {
			loadImage();
		}
	}
</script>

<div
	class="lazy-image-container {className}"
	style="width: {typeof width === 'number' ? width + 'px' : width}; height: {typeof height ===
	'number'
		? height + 'px'
		: height};"
	use:lazyLoad={{ onEnter: handleIntersection, rootMargin: '100px', once: true }}
>
	{#if !isIntersecting && loading === 'lazy'}
		<div class="image-placeholder">
			{#if placeholder}
				<img src={placeholder} {alt} class="placeholder-img" />
			{:else}
				<div class="placeholder-skeleton"></div>
			{/if}
		</div>
	{:else if hasError}
		<div class="image-error">
			<div class="error-icon">üñºÔ∏è</div>
			<span>Image failed to load</span>
		</div>
	{:else}
		<picture>
			{#if webpSrcset}
				<source srcset={webpSrcset} sizes={sizes || '100vw'} type="image/webp" />
			{/if}
			<img
				bind:this={imageElement}
				{src}
				srcset={responsiveSrcset}
				sizes={sizes || '100vw'}
				{alt}
				class="lazy-image {isLoaded ? 'loaded' : ''}"
				on:load={handleLoad}
				on:error={handleError}
				decoding="async"
				{loading}
			/>
		</picture>
	{/if}
</div>

<style>
	.lazy-image-container {
		position: relative;
		overflow: hidden;
		background: #1a1a1a;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 8px;
	}

	.lazy-image {
		width: 100%;
		height: 100%;
		object-fit: cover;
		transition: opacity 0.3s ease;
		opacity: 0;
	}

	.lazy-image.loaded {
		opacity: 1;
	}

	.image-placeholder {
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.placeholder-img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		filter: blur(5px);
		transform: scale(1.1);
	}

	.placeholder-skeleton {
		width: 100%;
		height: 100%;
		background: linear-gradient(90deg, #2a2a2a 25%, #3a3a3a 50%, #2a2a2a 75%);
		background-size: 200% 100%;
		animation: loading 1.5s infinite;
	}

	@keyframes loading {
		0% {
			background-position: 200% 0;
		}
		100% {
			background-position: -200% 0;
		}
	}

	.image-error {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		color: #666;
		font-size: 0.875rem;
		width: 100%;
		height: 100%;
		min-height: 100px;
	}

	.error-icon {
		font-size: 2rem;
		margin-bottom: 0.5rem;
		opacity: 0.5;
	}
</style>
